import { StatusBar } from "expo-status-bar";
import React, { useState } from "react";
import { StyleSheet, View, Text } from "react-native";
import { SoundOnPress } from "./sound/index";

const letters = {
  alef: {
    qamats: {
      sign: "אָ",
      soundFile: "alef-qamats.ogg",
    },
    patah: {
      sign: "אַ",
      soundFile: "alef-qamats.ogg",
    },
    hiriq: {
      sign: "אִ",
      soundFile: "alef-hiriq.ogg",
    },
    hiriqMale: {
      sign: "אִי",
      soundFile: "alef-hiriq.ogg",
    },
    holam: {
      sign: "אֹ",
      soundFile: "alef-holam.ogg",
    },
    holamMale: {
      sign: "אוֹ",
      soundFile: "alef-holam.ogg",
    },
    holamMale: {
      sign: "אוֹ",
      soundFile: "alef-holam.ogg",
    },
    shuruk: {
      sign: "אוּ",
      soundFile: "alef-shuruk.ogg",
    },
    kubuts: {
      sign: "אֻ",
      soundFile: "alef-shuruk.ogg",
    },
    tsere: {
      sign: "אֵ",
      soundFile: "alef-tsere.ogg",
    },
  },
};

export function Main() {
  const [letter, setLetter] = useState(getRandomLetter(letters));
  const { sign, soundFile } = letter;
  return (
    <View style={styles.container}>
      <SoundOnPress
        soundFile={soundFile}
        afterSound={() => setLetter(getRandomLetter(letters))}
      >
        <Text style={[styles.letter, { color: getRandomColor() }]}>{sign}</Text>
      </SoundOnPress>
      <StatusBar style="auto" />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  letter: {
    fontSize: 400,
  },
});

const getRandomLetter = (letters) => {
  const letterMarks = getRandomValue(letters);
  return getRandomValue(letterMarks);
};

const getRandomValue = (obj) => {
  const keys = Object.keys(obj);
  const idx = Math.floor(Math.random() * keys.length);
  return obj[keys[idx]];
};

function getRandomColor() {
  var letters = "0123456789ABCDEF";
  var color = "#";
  for (var i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
}
