import React from "react";
import { Provider as PaperProvider, DefaultTheme } from "react-native-paper";
import { Main } from "./src/main";

const theme = {
  ...DefaultTheme,
  dark: true,
  roundness: 2,
};
export default function App() {
  return (
    <PaperProvider theme={theme}>
      <Main />
    </PaperProvider>
  );
}
